import React, { Component } from 'react';
// import axios from 'axios';
import { Route, NavLink, Switch, Redirect } from 'react-router-dom';

import './Blog.css';
import Posts from './Posts/Posts';
import asyncComponent from '../../hoc/asyncComponent';
import NewPost from './NewPost/NewPost';
import NewPrivileges from './NewPrivileges/NewPrivileges';
import NewUser from './NewUser/NewUser';
import NewArea from './NewArea/NewArea';

const AsyncNewPost = asyncComponent(() => {
    return import('./NewPost/NewPost');
});
const AsyncNewUser = asyncComponent(() => {
    return import('./NewUser/NewUser');
});
const AsyncNewPrivileges = asyncComponent(() => {
    return import('./NewPrivileges/NewPrivileges');
});
const AsyncNewArea = asyncComponent(() => {
    return import('./NewArea/NewArea');
});

class Blog extends Component {
    state = {
        auth: true
    }

    render () {
        return (
            <div className="Blog">
                <header>
                    <nav>
                        <ul>
                            <li><NavLink
                                to="/posts/"
                                exact
                                activeClassName="my-active"
                                activeStyle={{
                                    color: '#fa923f',
                                    textDecoration: 'underline'
                                }}>posts</NavLink></li>
                            <li><NavLink to={{
                                pathname: '/new-post',
                                hash: '#submit',
                                search: '?quick-submit=true'
                            }}>Perfiles</NavLink></li>
                            <li><NavLink to={{
                                pathname: '/new-privileges',
                                hash: '#submit',
                                search: '?quick-submit=true'
                            }}>Privilegios</NavLink></li>
                            <li><NavLink to={{
                                pathname: '/new-area',
                                hash: '#submit',
                                search: '?quick-submit=true'
                            }}>Areas</NavLink></li>
                            <li><NavLink to={{
                                pathname: '/new-user',
                                hash: '#submit',
                                search: '?quick-submit=true'
                            }}>Usuarios</NavLink></li>
                        </ul>
                    </nav>
                </header>
                {/* <Route path="/" exact render={() => <h1>Home</h1>} />
                <Route path="/" render={() => <h1>Home 2</h1>} /> */}
                <Switch>
                    {this.state.auth ? <Route path="/new-post" component={AsyncNewPost} /> : null}
                    {this.state.auth ? <Route path="/new-privileges" component={AsyncNewPrivileges} /> : null}
                    {this.state.auth ? <Route path="/new-user" component={AsyncNewUser} /> : null}
                    {this.state.auth ? <Route path="/new-area" component={AsyncNewArea} /> : null}
                    <Route path="/posts" component={Posts} />
                    <Route render={() => <h1>Not found</h1>}/>
                    {/* <Redirect from="/" to="/posts" /> */}
                    {/* <Route path="/" component={Posts} /> */}
                </Switch>
            </div>
        );
    }
}

export default Blog;


